// src/pages/index.tsx
import type { NextPage } from 'next';
import Head from 'next/head';
import Link from 'next/link';
import { useSession } from 'next-auth/react';
import { useRouter } from 'next/router';

import Navbar from '../components/Navbar';
import NuestrosServicios from '../components/NuestrosServicios';
import PoolSection from '../components/PoolSection';
import ContactForm from '../components/ContactForm';
import Footer from '../components/Footer';

const Home: NextPage = () => {
  const { data: session } = useSession();
  const router = useRouter();

  const handleHero = (e: React.MouseEvent) => {
    e.preventDefault();
    if (session) router.push('/dashboard?tab=reservar');
    else router.push('/login');
  };

  return (
    <>
      <Head>
        <title>Bloom Fisio | Reserva tu cita</title>
        <meta name="description" content="Reserva tu sesi√≥n de fisioterapia de forma f√°cil y segura en Bloom Fisio." />
        <link rel="icon" href="/images/logo_bloom_clean.png" />
      </Head>

      <Navbar />

      <header className="hero">
        <h1 className="display-5 fw-bold">Aumenta la calidad de vida en tu familia</h1>
        <p className="lead">Reserva tu sesi√≥n de fisioterapia de forma f√°cil y segura.</p>
        <a href="#reservar" className="btn-hero" onClick={handleHero}>
          <span>Agendar tu cita</span>
          <span className="underline" />
        </a>
      </header>

      <NuestrosServicios />
      <PoolSection />

      <section className="container py-5 text-center">
        <h2 className="mb-4">Recibe tips de bienestar y ofertas exclusivas</h2>
        <ContactForm />
      </section>

      <Footer />
    </>
  );
};

export default Home;
